import Search from '@/components/index/search.vue'
import listItem from '@/components/listItem.vue'
import { mapState,mapMutation } from 'vuex'
import Time from '@/common/time.js'
export default {
	data() {
		return {
			list:[],
			page:1,
			limit:8,
			value:'',
			del:false
		}
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey,
			newgoodsList:state=>state.newgoodsList,
			sessionKey:state=>state.sessionKey
		})
	},
	components:{
		Search,
		listItem
	},
	methods: {
		searchKey(){//关键字查询
			uni.showLoading({
				title:'查询中...'
			})
			if(this.value===''){
				return uni.showToast({
					title:'输入不能为空',
					icon:'none'
				})
			}
			this.$api.searchfor({
				keyword:this.value,
				page:this.page,
				limit:this.limit,
				sessionKey:this.sessionKey
			}).then(res=>{
				this.list=res.goods
				uni.hideLoading()
			})
		},
		delkey(){
			this.value=''
		},
		showdel(e){
			this.del=true
		},
		hidedel(){
			this.del=false
		},
		sortDesc(){
		},
		addSearch(e){
			this.searchKey()
		}
	},
	onLoad(e){
		this.value=e.keyword
		this.searchKey()
	}
}
